        text1: '去青海多给自己一些时间，因为等你回去后多数会后悔当时呆的时间太短了。那里早晚温差大，多带件外套，如果晚上睡湖边的那些蒙古包之类的，就算7.8月份，依然可能会感觉冷。最好的方式是自驾或者骑行，那样更方便停下来近距离的欣赏。二郎剑是打卡标配地点，人多！！但还是愿意花上一天进去看看。这次熊猫国旅品质游安排的很好，省去了排队时间，抢停车位的时间，留出更多时间去玩，去欣赏~不会像纯驴友那么累，多了点闲暇时光',
