<template>
    <div class="det-qut det-qut_v2">
    <h2>{{ titleFrom.title }}</h2>
	
    <div id="j_qinfo" class="">
      <div class="info">
        <div class="text">
            <p class="q_content"></p>
            <p><span style="color:#666666;font-family:'Microsoft YaHei';font-size:1rem;line-height:1.4;">{{ titleFrom.text }}</span></p>
            <div class="q-desc " style="padding:0px;margin:10px 0px 0px;">
                <div>
                    <span style="font-size:1.6rem;font-family:'Microsoft YaHei';color:#666666;"></span> 
                </div>
            </div>
        </div>
        <div class="author"> <img :src="titleFrom.authorImg">{{ titleFrom.authorName }} <span class="date">2018-6-16</span> </div>
      </div>
    </div>
    <div class="follow clearfix">
      <div class="fl-l det-nub"><span class="mdd">{{ titleFrom.authorMdd }}</span><strong>·</strong>{{ titleFrom.browse }}浏览<strong>·</strong>{{ titleFrom.focusOn }}关注</div>
      <div class="fl-r det-att"> </div>
    </div>
  </div>
</template>

<script>
export default {
    props: {
        titleFrom: {
            type: Object,
            default: {}
        }
    },
    data() {
        return {

        }
    }
}
</script>

<style scoped>
.det-qut_v2 {
  padding: 16px 12px 15px;
  background-color: #fffef0;
}
.det-qut_v2 h2 {
  font-size: 20px;
  line-height: 27px;
  font-weight: normal;
  color: #111;
}
.det-qut_v2 .info {
  padding-top: 8px;
}
.det-qut_v2 .author {
  font-size: 15px;
  line-height: 20px;
  margin-top: 10px;
  color: #666;
}
.det-qut_v2 .author img {
  width: 20px;
  height: 20px;
  margin: 0 5px 0 0;
}
.det-qut_v2 .author .date {
  margin-left: 10px;
  color: #999;
}
.det-qut_v2 .follow {
  padding: 5px 0 0 0;
}
.det-qut_v2 .det-nub {
  line-height: 30px;
}
.det-qut_v2 .det-nub strong {
  padding: 0 5px;
}
.fl-l {
  float: left;
}
</style>



