<template>
    <li class="a-list_v2 clearfix">
        <div class="user">
            <a>
                <img :src="ans.authorImg">
            </a>
        </div>
        <div class="con">
            <div class="user-bar">
                <div class="user-vertical">
                    <a>{{ ans.authorName }}</a> <span class="lv">LV.{{ ans.lv }}</span>
                </div>
                <ul class="answer-medal detail_answer">
                </ul>
            </div>
            <a class="a-desc">
                <p></p>
                <p><span v-html="ans.text1"></span></p>
                <p><img :src="ans.img1" width="400" alt=""></p>
                <p><span v-html="ans.text2"></span></p>
                <p></p>
            </a>
            <div class="a-btns">
                <a class="btn1 ding clearfix">
                    <i class="_j_do_up "></i><strong class="_j_hnum">{{ ans.read }}</strong><span>&nbsp;· {{ ans.commentNum }}评论</span>
                </a>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    props: {
        ans: {
            type: Object,
            default: {}
        }
    },
    data() {
        return {
            
        }
    }
}
</script>

<style scoped>
.a-list_v2 {
  padding: 20px 12px 10px;
  position: relative;
}
.a-list_v2 .user {
  width: 42px;
  padding-top: 0;
}
.a-list_v2 .user img {
  width: 32px;
  height: 32px;
}
.a-list_v2 .user-bar {
  height: 32px;
  line-height: 14px;
  padding-top: 0;
  position: static;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-direction: wrap;
  -webkit-align-items: center;
  align-items: center;
}
.a-list_v2 .answer-medal {
  right: 0;
  top: 0;
}
.a-list_v2 .user-bar a {
  color: #111;
}
.a-list_v2 .user-bar .lv {
  padding: 0 0.2rem;
  font-size: 12px;
  line-height: 0.9rem;
  border-radius: 0.3rem;
}
.a-list_v2 .a-desc {
  padding-left: 0;
  padding-top: 10px;
  line-height: 22px;
  overflow: hidden;
  clear: both;
}

/* 正文样式 */
.a-list_v2 .a-desc p {
  color: #696969;
  max-height: 1000px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  margin-bottom: 10px;
  font-size: 1rem;
}

/* 阅读数与评论 */
.a-list_v2 .a-btns {
  margin: 4px 0 0 0;
  padding: 0;
  height: 30px;
  line-height: 30px;
}
.a-list_v2 .a-btns a {
  line-height: 30px;
}
.a-list_v2 .a-btns .btn1 {
  font-weight: normal;
  font-size: 15px;
}
.a-list_v2 .a-btns .btn1 span {
  font-size: 15px;
}
</style>



